// Variables for Google Fonts font stacks
$font-heading: 'Raleway', sans-serif;
$font-body: 'Montsterrat', sans-serif; 

// Variables for type scale
$font-size-h1: 2.488rem;
$font-size-h2: 2.074rem;
$font-size-h3: 1.728rem;
$font-size-h4: 1.44rem;
$font-size-h5: 1.2rem;

// Reset default margin and padding for typography elements
h1, h2, h3, h4, h5, h6, p {
    margin: 0; padding: 0;
} 

// Set Heading font for heading elements
h1, h2, h3, h4, h5, h6 {
    font-family: $font-heading;
    font-weight: 300;
}

p {
    font-family: $font-body;
}

h1 {
    font-size: $font-size-h1;
}